，文化技藝再生，及「六級產業」等各種可能性。當此四面向與社會、文化、藝術、經濟交會，讓食農議題有更創新的合作面向。期盼藉著本活動讓參與者合作激盪創意，並提出解決食農與改變社會的方案，並為將來更廣泛而長遠的合作機制寫下序章。\n                ")])])]),_c('div',{staticClass:"text-center"},[_c('div',{staticClass:"md-layout"},[_c('div',{staticClass:"md-layout-item md-medium-size-50 md-small-size-100"},[_c('div',{staticClass:"info"},[_c('div',{staticClass:"icon icon-warning"},[_c('md-icon',{staticClass:"md-size-2x"},[_vm._v("new_releases")])],1),_vm._m(1),_c('p',{staticClass:"description text-center"},[_vm._v("\n                            公布 15 隊入選名單\n                        ")]),_c('p',{staticClass:"text-center"},[_c('md-button',{staticClass:"md-success md-lg",attrs:{"href":_vm.getImgURL('files/初選入圍名單.jpg'),"target":"_blank","disabled":false}},[_vm._v("\n                                初選入圍名單\n                            ")])],1)])]),_c('div',{staticClass:"md-layout-item md-medium-size-50 md-small-size-100"},[_c('div',{staticClass:"info"},[_c('div',{staticClass:"icon icon-danger"},[_c('md-icon',[_vm._v("stars")])],1),_vm._m(2),_c('p',{staticClass:"description text-center"},[_c('md-icon',[_vm._v("room")]),_vm._v(" 空總臺灣當代文化實驗場\n                            "),_c('br'),_vm._v("聯合餐廳 1 樓灰盒子空間\n                            "),_c('br'),_vm._v("（ 台北市大安區建國南路一段 177 號 ）\n                        ")],1)])])])])])])}
